@import "../../../style/mainStyle.scss";
@import "../../../style/mixin.scss";

.quoteFullForm {
    @include form($mainLightColor, 90%, 100%);
    background-color: rgba(255, 255, 255, 0.9);
    padding-bottom: 20px;
    padding-left: 5px;
    font-weight: 500; 
}


.quoteFullFormOpacity{
    @include form($mainLightColor, 90%, 100%);
    background-color: rgba(161, 154, 154, 0.9);
    padding-bottom: 20px;
    padding-left: 5px;
    font-weight: 500; 
    opacity: 0.1;
}

.proposedOptionBlockText{
    margin-top: 20px;
    text-align: center;
    font-size: 18px;
    color: $fourthColor;
    text-decoration: underline;
    font-weight: 700;
}